  }
}
:root {
  --background: #ffffff;
  --foreground: #171717;
}
* {
  margin: 0;
  padding: 0;
}
body {
  color: var(--foreground);
  background: var(--background);
  font-family: "Poppins", sans-serif;
}
.dot {
  color: #cc00ff;
  font-size: 2.5rem;
  line-height: 0;
  vertical-align: baseline;
  display: inline-block;
  transform: translateY(-10%);
}
html {
  scroll-behavior: smooth;
}
p {
  color: rgb(85, 85, 85);
}
a, .btn {
  transition: all 300ms ease;
}
nav, .nav-links {
  display: flex;
}
nav {
  justify-content: space-around;
  align-items: center;
  height: 17vh;
}
.nav-links {
  gap: 2rem;
  list-style: none;
  font-size: 1.5rem;
}
a {
  color: black;
  text-decoration: none;
  text-decoration-color: white;
}
a:hover {
  color: grey;
  text-decoration: underline;
  text-underline-offset: 1rem;
  text-decoration-color: rgb(181, 181, 181);
}
.logo {
  font-size: 2rem;
}
.logo:hover {
  cursor: default;
}
.hamburger-nav {
  display: none;
}
.hamburger-menu {
  display: flex;
  position: relative;
  display: inline-block;
}
.hamburger-icon {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 24px;
  width: 30px;
  cursor: pointer;
}
.hamburger-icon span {
  width: 100%;
  height: 2px;
  background-color: black;
  transition: all 0.3 ease-in-out;
}
.menu-links {
  position: absolute;
  top: 100%;
  right: 0%;
  background-color: white;
  width: fit-content;
  max-height: 0;
  overflow: hidden;
  transition: all 0.3 ease-in-out;
}
.menu-links a {
  display: block;
  padding: 10px;
  text-align: center;
  font-size: 1.5rem;
  color: black;
  text-decoration: none;
  transition: all 0.3 ease-in-out;
}
.menu-links li {
  list-style: none;
}
.menu-links.open {
  max-height: 300px;
}
.hamburger-icon.open span:first-child {
  transform: rotate(45deg) translate(10px, 5px);
}
.hamburger-icon.open span:nth-child(2) {
  opacity: 0;
}
.hamburger-icon.open span:last-child {
  transform: rotate(-45deg) translate(10px, -5px);
}
.hamburger-icon span:first-child {
  transform: none;
}
.hamburger-icon span:first-child {
  opacity: 1;
}
.hamburger-icon span:first-child {
  transform: none;
}
section {
  padding-top: 0;
  height: 96vh;
  margin: 0 10rem;
  box-sizing: border-box;
  min-height: fit-content;
}
.section-container {
  display: flex;
}
.profile {
  display: flex;
  justify-content: center;
  gap: 5rem;
  height: 80vh;
}
.section__pic-container {
  background: url('/portrait.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin: auto 0;
  height: 400px;
  width: 400px;
  box-shadow: inset 0 0 0 9px rgba(255, 255, 255, 0.3);
  justify-self: center;
  animation: profile__animate 8s ease-in-out infinite 1s;
}
@keyframes profile__animate {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
  }
  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
}
.section__text {
  align-self: center;
  text-align: center;
}
.section__text p {
  font-weight: 600;
}
.section__text__p1 {
  text-align: center;
}
.section__text__p2 {
  font-size: 1.75rem;
  margin-bottom: 1rem;
}
.title {
  font-size: 3rem;
  text-align: center;
  font-weight: 700;
}
.socials-container {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
  gap: 1rem;
}
.icon {
  cursor: pointer;
}
.btn-container {
  display: flex;
  justify-content: center;
  gap: 1rem;
}
.btn {
  font-weight: 600;
  transition: all 300ms ease;
  padding: 1rem;
  width: 10rem;
  border-radius: 2rem;
  font-size: 0.8rem;
}
.btn-color-1, .btn-color-2 {
  border: rgb(53,53,53) 0.1rem solid;
}
.btn-color-1:hover, .btn-color-2:hover {
  cursor: pointer;
}
.btn-color-1, .btn-color-2:hover {
  background: rgb(53,53,53);
  color: white;
}
.btn-color-1:hover {
  background: rgb(0,0,0);
}
.btn-color-2 {
  background: none;
}
.btn-color-2:hover {
  border: rgb(255,255,255) 0.1rem solid;
}
.btn-container {
  gap: 1rem;
}
.about-containers {
  margin-top: 20vh;
  margin: 0 auto;
  display: flex;
}
.terminal {
  max-width: 50vw;
  margin: 0 auto;
}
.terminal-header {
  height: 28px;
  background-color: #e4e3e5;
  border-radius: 10px 10px 0 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.header-button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-left: 9px;
}
.red {
  background-color: #f96256;
  border: solid 1px #f65549;
}
.yellow {
  background-color: #fdbc3d;
  border: solid 1px #ffb524;
}
.green {
  background-color: #33c948;
  border: solid 1px #2dbb41;
}
.terminal-window {
  background-color: #595D7A;
  border-radius: 0 0 4px 4px;
  box-shadow: 0 50px 100px rgba(50, 50, 93, 0.15),
    0 15px 35px rgba(50, 50, 93, 0.2), 0 5px 15px rgba(0, 0, 0, 0.12);
  font-family: 'Monaco', Consolas, 'Lucida Console', monospace;
}
.terminal-window a {
  color: var(--link-teal, #008080);
}
.statement:first-child {
  padding-top: 35px;
}
.statement:last-child {
  padding-bottom: 60px;
}
.statement {
  margin-bottom: 25px;
  margin-left: 35px;
  margin-right: 35px;
  line-height: 24px;
}
.input-statement {
  color: #f7f7f7;
}
.input-statement::before {
  content: '>\a0';
}
.return-statement {
  color: #e7d184;
}
span {
  background: rgba(247, 247, 247, 0.65);
  animation: caret 1s steps(1) infinite;
}
@keyframes caret {
  50% {
    background-color: transparent;
  }
}
.about {
  position: relative;
  max-height: 70vh;
}
.arrow {
  position: absolute;
  right: -5rem;
  bottom: 2.5rem;
}
.about-containers {
  gap: 2rem;
  margin-bottom: 2rem;
